header {
    padding-bottom: 94px;
}

.HeaderNavigation {
    height: 90px;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 2;
    background-color: rgba(32, 32, 32, 0.95);
    border-bottom: 6px solid #3d82a7;
    -webkit-box-shadow: rgba(0, 0, 0, .1) 0 3px 5px;
            box-shadow: rgba(0, 0, 0, .1) 0 3px 5px;
}

.HeaderNavigation .header-container {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.HeaderNavigation .left, .HeaderNavigation .right {
    -ms-flex: 1 1;
        flex: 1 1;
}

.HeaderNavigation a {
    text-align: center;
    text-decoration: none;
    vertical-align: middle;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    line-height: 90px;
}

.HeaderNavigation .left {
    text-align: left;
}

.HeaderNavigation img {
    vertical-align: middle;
    height: 60px;
    background-color: white;
    border-radius: 50%;
}

.headline {

}

.HeaderNavigation .right {
    text-align: right;
}

.HeaderNavigation .right a {
    margin-left: 10px;
}

@media (max-width: 530px) {
    header {
        padding-bottom: 69px;
    }

    .HeaderNavigation {
        height: 65px;
    }

    .HeaderNavigation a {
        line-height: 65px;
    }

    .HeaderNavigation img {
        vertical-align: middle;
        height: 45px;
    }

    .HeaderNavigation .right a {
        font-size: 1.1em;
    }
}


